.header {
    padding: 15px 0;
    &-contacts {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        &__phone {
            &s {
                display: flex; }
            font-size: 22px;
            margin-right: 13px;
            &:first-child {
                border-right: 1px solid #cfcfd0;
                padding-right: 13px; } } }
    &-logo {
        background: url(../img/logo.jpg) center no-repeat;
        background-size: 100%;
        width: 191px;
        height: 61px; } }
