.main {
    padding-top: 65px;
    padding-bottom: 60px;
    background: #373737 url('../img/background.jpg') center no-repeat;
    background-size: cover;
    color: $text-color-light;
    z-index: 2;
    &-img {
        position: relative;
        &::before {
            content: '';
            position: absolute;
            width: 390px;
            height: 390px;
            border: 1px solid #ffffff;
            z-index: -1;
            border-radius: 50%;
            left: 50%;
            margin-left: -195px; } }
    &-text {
        &__title {
            margin-bottom: 20px;
            font-size: 41px;
            font-weight: 700; }
        &__subtitle {
            margin-bottom: 27px;
            font-size: 28px;
            font-weight: 300; }
        &__form {
            padding: 30px 27px;
            background-color: rgba(255, 255, 255, 0.2); } } }
.form {
    display: flex;
    margin-bottom: 12px;
    &-title {
        margin-bottom: 20px;
        padding-right: 87px;
        font-size: 20px;
        font-weight: 300;
        line-height: 30px;
        &__important {
            display: block;
            font-weight: 700;
            text-transform: uppercase; } }
    &__input {
        border-radius: 4px;
        border: none;
        padding: 20px;
        vertical-align: middle;
        margin-right: 10px;
        width: 100%;
        max-width: 365px; }
    &-small {
        font-size: 14px;
        font-weight: 300; } }






