.section {
    &-title {
        font-size: 27px;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 29px;
        &::after {
            content: '';
            display: block;
            margin-top: 12px;
            height: 6px;
            background: url(../img/wave.png) center no-repeat; } } }
.button {
    font-size: 14px;
    background-color: $active-color;
    border: 1px solid transparent {
        radius: 4px; }
    color: $text-color-light;
    padding: 13px 22px;
    &__big {
        padding-top: 18px;
        padding-bottom: 18px;
        vertical-align: middle;
        width: 30%; }
    &:hover {
        background-color: lighten($active-color, 5);
        cursor: pointer; }
    &-o {
        background-color: transparent;
        color: $text-color-dark;
        border: 1px solid rgba(#373737, 0.5);
        &:hover {
            background-color: lighten($active-color, 5);
            color: $text-color-light;
            border: 1px solid transparent;
            cursor: pointer; } }
    img {
        vertical-align: middle; } }
.slider-arrow {
    position: absolute;
    top: 0;
    z-index: 99;
    height: 100%;
    width: 22px;
    &_prod {
        border: 1px solid $active-color; }
    &:hover {
        cursor: pointer; }
    &_right {
        right: 0;
        background: url(../img/arrow-right.png) center no-repeat; } }
.dark-bg {
    position: relative;
    z-index: 2;
    &::before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: -1; } }

