@media ( max-width: 768px ) {
    .hidden-mobile {
        display: none; }
    .header {
        &-logo {
            background: $active-color url(../img/logo-mobile.png) center no-repeat;
            width: 35px;
            height: 35px;
            border-radius: 4px; }
        &-contacts {
            display: flex;
            &__phone {
                &s {
                    flex-direction: column; }
                font-size: 14px;
                margin-right: 0;
                &:first-child {
                    border-right: none; } }
            &__button {
                display: inline-block;
                padding: 9; } } } }
@media ( max-width: 576px ) {
    .main {
        text-align: center;
        padding-top: 50px;
        &-text {
            &__title {
                font-size: 22px;
                color: $active-color; }
            &__subtitle {
                font-size: 18px;
                margin-bottom: 40px; }
            &__form {
                background-color: transparent;
                padding: 10px; } } }
    .form {
        display: block;
        &-title {
            padding-right: 0;
            font-size: 15px;
            line-height: 1.4; }
        &__input {
            display: block;
            margin-bottom: 13px; } }
    .numbers {
        &-block {
            display: block;
            width: 100%;
            margin-bottom: 24px; } }
    .features {
        &-blocks {
            flex-direction: column; } }
    .button {
        padding: 10px 15px; }
    .production {
        &-slider_bottom {}
        margin-top: 22px;
        height: auto;
        &-text {
            text-align: center;
            &__title {
                padding-left: 0;
                margin-left: 0;
                width: 100%; }
            &__descr {
                text-align: center; } } }
    .contacts-block {
        position: relative;
        text-align: center;
        top: 0;
        left: 0; } }
